"use client";

import React, { useEffect, useState } from "react";
import Link from "next/link";
import FridayReminder from "../components/FridayReminder";
import { Sun, Moon } from "lucide-react";
import BottomNav from "../components/BottomNav";

/**
 * Root layout for the Oficina da Tecnologia app.
 * Includes navigation links, dark mode toggle, and the Friday reminder component.
 * Wraps all pages with consistent layout and styling.
 */

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  // State for dark mode
  const [darkMode, setDarkMode] = useState(false);

  // On mount, check localStorage for dark mode preference
  useEffect(() => {
    const savedMode = localStorage.getItem("darkMode");
    if (savedMode === "true") {
      setDarkMode(true);
      document.documentElement.classList.add("dark");
    }
  }, []);

  // Toggle dark mode and save preference
  const toggleDarkMode = () => {
    if (darkMode) {
      document.documentElement.classList.remove("dark");
      localStorage.setItem("darkMode", "false");
      setDarkMode(false);
    } else {
      document.documentElement.classList.add("dark");
      localStorage.setItem("darkMode", "true");
      setDarkMode(true);
    }
  };

  return (
    <html lang="pt-BR" className={darkMode ? "dark" : ""}>
      <body className="bg-indigo-50 dark:bg-gray-900 min-h-screen flex flex-col transition-colors duration-300">
        <header className="bg-indigo-700 dark:bg-gray-800 text-white p-4 shadow-md">
          <nav className="max-w-5xl mx-auto flex justify-between items-center">
            <h1 className="text-xl font-bold">Oficina da Tecnologia</h1>
            <ul className="flex space-x-6 items-center">
              <li>
                <Link href="/" className="hover:underline">
                  InÃ­cio
                </Link>
              </li>
              <li>
                <Link href="/lesson" className="hover:underline">
                  Aula da Semana
                </Link>
              </li>
              <li>
                <Link href="/practical" className="hover:underline">
                  Exemplo PrÃ¡tico
                </Link>
              </li>
              <li>
                <Link href="/progress" className="hover:underline">
                  Progresso
                </Link>
              </li>
              <li>
                <button
                  onClick={toggleDarkMode}
                  aria-label="Alternar modo escuro"
                  className="ml-4 p-2 rounded-full hover:bg-indigo-600 dark:hover:bg-gray-700 transition"
                >
                  {darkMode ? (
                    <Sun className="w-6 h-6 text-yellow-400" />
                  ) : (
                    <Moon className="w-6 h-6" />
                  )}
                </button>
              </li>
            </ul>
          </nav>
        </header>

        <main className="flex-grow max-w-5xl mx-auto w-full p-4">{children}</main>

        <FridayReminder />

        <footer className="bg-indigo-700 dark:bg-gray-800 text-white p-4 text-center">
          Â© 2024 Oficina da Tecnologia. Todos os direitos reservados.
        </footer>

        {/* Persistent bottom navigation */}
        <div className="pt-16">
          <BottomNav />
        </div>
      </body>
    </html>
  );
}
