"use client";

import React, { useEffect, useState } from "react";

/**
 * FridayReminder Component
 * Shows an in-app reminder message if today is Friday.
 * Uses JavaScript Date to check the current day.
 */

export default function FridayReminder() {
  const [isFriday, setIsFriday] = useState(false);

  useEffect(() => {
    const today = new Date();
    // getDay() returns 5 for Friday (0=Sunday, 1=Monday, ..., 6=Saturday)
    if (today.getDay() === 5) {
      setIsFriday(true);
    }
  }, []);

  if (!isFriday) return null;

  return (
    <div className="fixed bottom-4 right-4 bg-indigo-600 text-white p-4 rounded-lg shadow-lg max-w-xs">
      <p className="font-semibold">Lembrete: Nova aula disponÃ­vel hoje! ðŸŽ‰</p>
    </div>
  );
}
